import { useRef, useEffect, useState } from "react";
import { Toast } from 'primereact/toast';

import { InputNumber } from 'primereact/inputnumber';

const BlockBet = (data) => {

    const [sumBet, setSumBet] = useState(null);
    const [kefBet, setKefBet] = useState(null);

    const socket = data.socket;

    let toast = useRef(null);

    function changeStateCurrency(e) {
        document.querySelector('.active').classList.remove('active');
        e.target.classList.add('active');
    }

    function setBet(e) {

        let token = localStorage.getItem('Token');
        if (token) token = JSON.parse(token);

        let valSumBet = sumBet;
        let valKefBet = kefBet;
        let valBetCurrency = document.querySelector('.game_bet_block_row__currency.active').textContent;

        if (!valKefBet || !valSumBet) {
            toast.current.show({severity:'warn', summary: 'Warning Message', detail: 'Bet amount and odds are not filled', life: 5000});
            return;
        } else if (valSumBet <= 0) {
            toast.current.show({severity:'warn', summary: 'Warning Message', detail: 'Bet amount must be > 0', life: 5000});
            return;
        } else if (valKefBet <= 0) {
            toast.current.show({severity:'warn', summary: 'Warning Message', detail: 'Auto cash out must be > 0', life: 5000});
            return;
        }

        let payload = { token, sumBet: valSumBet, kefBet: valKefBet, currencyBet: valBetCurrency }

        socket.emit('setBet', payload);

    }

    useEffect(() => {
        socket.on('setBet:result', data => {
            if (data.ok) {
                toast.current.show({ severity:'success', summary: 'Success', detail: 'Bid accepted. Good luck!', life: 5000 });
            } else {
                if (data.access === false) {
                    
                    localStorage.removeItem('Token');
                    window.location.reload();
    
                }
    
                toast.current.show({ severity:'error', summary: 'Error Message', detail: data.text, life: 5000 });
            }
        })
    }, [])

    if (data.isLoginUser != null && data.isLoginUser != false) {

        const windowInnerWidth = window.innerWidth;
        let balance = JSON.parse(localStorage.getItem('Balance'));

        if (windowInnerWidth < 700) {
            return(
                <div className="game_bet_block">
                    <div className="game_bet_block_row">
                        <p>Bet</p>
                        <InputNumber value={sumBet} onChange={(e) => setSumBet(e.value)} className="w-12" mode="decimal" minFractionDigits={0} maxFractionDigits={5} />
                        <div className="game_bet_block_row__currency active" id="BTC" onClick={ changeStateCurrency }>BTC</div>
                        <div className="game_bet_block_row__currency" id="ETH" onClick={ changeStateCurrency }>ETH</div>
                    </div>
                    <div className="game_bet_block_row">
                        <p>Auto cash out</p>
                        <InputNumber value={kefBet} onChange={(e) => setKefBet(e.value)} className="w-12" mode="decimal" minFractionDigits={2} maxFractionDigits={2} step={0.01} showButtons={true} />
                    </div>
                    <div className="game_bet_block_row">
                        <button className="set_bet" disabled={ balance.btc === 0 && balance.eth === 0 }>BET</button>
                    </div>
                    <Toast ref={ toast } />
                </div>
            );
        }

        return(
            <div className="game_bet_block">
                <div className="game_bet_block_row">
                    <div className="game_bet_block_row__currency active" id="BTC" onClick={ changeStateCurrency }>BTC</div>
                    <div className="game_bet_block_row__currency" id="ETH" onClick={ changeStateCurrency }>ETH</div>
                </div>
                <div className="game_bet_block_row">
                    <p>Bet</p>
                    <InputNumber value={sumBet} onChange={(e) => setSumBet(e.value)} className="w-12" mode="decimal" minFractionDigits={0} maxFractionDigits={5} />
                </div>
                <div className="game_bet_block_row">
                    <p>Auto cash out</p>
                    <InputNumber value={kefBet} onChange={(e) => setKefBet(e.value)} className="w-12" mode="decimal" minFractionDigits={0} maxFractionDigits={2} step={0.01} showButtons={true} />
                </div>
                <div className="game_bet_block_row">
                    <button className="set_bet" disabled={ balance.btc === 0 && balance.eth === 0 } onClick={ setBet } >BET</button>
                </div>
                <Toast ref={ toast } />
            </div>
        );

    } else {

        return(
            <div className="block_game__bet">
              <div className="game_bet__row">
                <a className="login_a" href="/login"><button>Login to play</button></a>
              </div>
              <div className="game_bet__row">
                or <a href="/register">register</a>
              </div>
            </div>
        );

    }
}

export default BlockBet;